@import '../variables.scss';

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: 0.4s;
    padding: 40px 100px;

    @include breakpoint(md) {
        flex-direction: row;
        justify-content: space-between;
    }

    &__title {
        display: none;
        font-size: 65px;
        font-weight: 600;
        color: $primary;

        @include breakpoint(lh) {
            display: flex;
        }

        &__short {
            display: flex;

            @include breakpoint(lh) {
                display: none;
            }
        }
    }

    &__navigation {
        display: none;

        @include breakpoint(md) {
            display: flex;
        }

        &__navbar {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;

            &__link {
                position: relative;
                list-style: none;
                font-size: 25px;
                margin: 0 15px;
                color: $light;
                transition: 0.3s ease all;

                &:hover {
                     color: $lartofto;
                     cursor: pointer;
                }
            }
        }
    }
}

.sticky {
    background-color: $lartofto;
    padding: 0 30px;

    .header__navigation__navbar__link {
        &:hover {
            color: $dark;
        }
    }
}

