$primary: #b8e0fc;
$dark: #152124;
$lartofto: #325064;
$light: #e0e0e0;

@mixin breakpoint($point) {
    @if $point == md {
        @media (min-width: 1370px) {
            @content;
        }
    }

    @if $point == lh {
        @media (min-width: 720px) {
            @content;
        }
    }
}

@mixin breakpointCard() {
    @media (max-width: 1262px) and (min-width: 982px) {
        @content;
    }
}